import Entity from '../entity/entity';
import Shell from '../entity/shell/shell';
import Slingshot from '../entity/slingshot';
import CommissarEnemy from '../entity/enemy/commissar-enemy';
import Phaser from '../lib/phaser';
import Level5Data from './data/level-5.data';
import addEnvironment from './shared/add-environment';
import create from './shared/create';
import initDebugCamera from './shared/init-debug-camera';
import preload from './shared/preload';
import update from './shared/update';
import randomBetween from '../utils/random-between';
import distance from '../utils/distance';
import createDisappearingEvent from '../ui/create-disappearing-event';

export default class Level5 extends (<any>Phaser.Scene) {
  enemies: Array<Entity> = [];
  shells: Array<Shell> = [];
  entities: Array<Entity>;
  slingshot: Slingshot;
  levelData: any = Level5Data;
  nextLevel: any;
  score: number = 0;
  winTimeout: number = 3000;
  zoom: number = 0.4;
  isWin: boolean;
  winTimeEvent: any;
  scoreText: any;
  commissarWithDialog: any;
  commissarsCount: number = 0;

  preload = preload.bind(this);
  initDebugCamera = initDebugCamera.bind(this);
  addEnvironment = addEnvironment.bind(this);

  cameraControls: any;
  distanceBetweenShellAndEnemy: number;
  enemyWidth: number = 700;

  constructor(config) {
    super(config);
  }

  create() {
    this.time.addEvent({
      delay: 250,
      callback: this.spawnCommissar,
      callbackScope: this,
      loop: true
    });

    let events = [
      this.commissarText(' ', 3000),
      this.commissarText(' -', 5000),
      this.commissarText(' ', 7000),
      this.commissarText('   ', 9000),
      this.commissarText('    ', 11000),
      this.commissarText(' ', 15000),
      this.commissarText('  ', 17000),
      this.commissarText('   ', 19000),
      this.commissarText('  ', 21000),
      this.commissarText('   ', 27000),
      this.commissarText(' ', 30000),
      this.commissarText('   30  ', 32000),
      this.commissarText('   ', 34000),
      this.commissarText('...', 38000),
      this.commissarText('  ', 42000),
      this.commissarText('  ', 43000),
      this.commissarText(' ', 44000),
      this.commissarText('  ', 45000),
      this.commissarText('  ', 47000),
      this.commissarText(' ...', 49000),
    ];

    events.forEach((v) => {
      this.time.addEvent({
        delay: v.delay,
        callback: () => {
          if (this.commissarWithDialog && !this.commissarWithDialog.isDestroyed) {
            createDisappearingEvent(this, v.x, v.y, v.type, v.data, v.scale, v.duration);            
          }
        }
      });
    });

    create.bind(this)();

    this.commissarWithDialog = this.enemies[0];
    this.commissarsCount = 0;
  }

  update() {
    update.bind(this)();
  }

  commissarText(t, d) {
    return {
      type: 'text',
      data: t,
      x: 3850,
      y: -700,
      delay: d,
      duration: 2500,
      scale: 1.55
    };
  }

  spawnCommissar() {
    if (this.commissarsCount > 20) {
      return;
    }

    let x = randomBetween(0, 3000); // 3500         
    let y = -2000;

    let commissar = new CommissarEnemy(this, x, y, true);

    // , 
    if (!this.enemies.length) {
      this.commissarsCount += 1;
      commissar.destructionMomentum = Infinity;
      commissar.body.collisionFilter.category = 0x0002;

      this.entities.forEach((e) => {
        if (e.type === 'shell') {
          e.body.collisionFilter.mask = 0x0001;
        }
      });
    }

    let angularVelocityDirection = (Math.random() > 0.5) ? 1 : -1;

    commissar.sprite
      .setScale(randomBetween(0.5, 1))
      .setAngularVelocity(0.3 * angularVelocityDirection)
      .setDensity(1)
      .applyForce({
        x: 0,
        y: randomBetween(0, 100)
      });
  }
}