import Entity from '../entity/entity';
import Shell from '../entity/shell/shell';
import Slingshot from '../entity/slingshot';
import Phaser from '../lib/phaser';
import Level4 from './level-4';
import Level3Data from './data/level-3.data';
import addEnvironment from './shared/add-environment';
import create from './shared/create';
import initDebugCamera from './shared/init-debug-camera';
import preload from './shared/preload';
import update from './shared/update';
import createDisappearingEvent from '../ui/create-disappearing-event';

export default class Level3 extends (<any>Phaser.Scene) {
  enemies: Array<Entity> = [];
  shells: Array<Shell> = [];
  entities: Array<Entity>;
  slingshot: Slingshot;
  levelData: any = Level3Data;
  nextLevel: any = Level4;
  winTimeout: number = 2500;
  score: number = 0;
  zoom: number = 0.35;
  isWin: boolean;
  winTimeEvent: any;
  scoreText: any;
  commissarWithDialog: any;

  preload = preload.bind(this);
  update = update.bind(this);
  initDebugCamera = initDebugCamera.bind(this);
  addEnvironment = addEnvironment.bind(this);

  cameraControls: any;

  constructor(config) {
    super(config);
  }

  create() {
    let events = [
      this.commissarText('', 5000),
      this.commissarText('', 7500),
      this.commissarText('', 10000),
      this.commissarText('', 10500),
      this.commissarText('', 11000),
      this.commissarText('', 13000),
      this.commissarText('', 14500),
      this.commissarText('', 16500),
      this.commissarText('  ', 20000),
      this.commissarText('   ))', 22000),
      this.commissarText('  ', 23000),
      this.commissarText('', 24000),
      this.commissarText('', 25000),
      this.commissarText('', 26000),
      this.commissarText('     ', 28000),
      this.commissarText(' ', 32000),
      this.commissarText(' ', 34000),
      this.commissarText('     ', 36000),
      this.commissarText('   ', 39000),
      this.commissarText('  )', 41000),
      this.commissarText('', 45000),
      this.commissarText('', 46000),
      this.commissarText('', 47000),
      this.commissarText('', 48000),
      this.commissarText('', 49000),
      this.commissarText('', 50000),
      this.commissarText('', 51000),
      this.commissarText(')))', 52000),
      this.commissarText('', 57000),
      this.commissarText('', 57500),
      this.commissarText('', 58000),
      this.commissarText('', 58500),
      {
        type: 'text',
        data: '  ',
        x: 700,
        y: 200,
        delay: 52000,
        duration: 2000,
        scale: 1.5
      },
      {
        type: 'text',
        data: '  ',
        x: 700,
        y: 200,
        delay: 54000,
        duration: 2000,
        scale: 1.5
      }
    ];

    events.forEach((v) => {
      this.time.addEvent({
        delay: v.delay,
        callback: () => {
          if (this.commissarWithDialog && !this.commissarWithDialog.isDestroyed) {
            createDisappearingEvent(this, v.x, v.y, v.type, v.data, v.scale, v.duration);            
          }
        }
      });
    });

    create.bind(this)();

    this.commissarWithDialog = this.enemies[4];

    this.add.sprite(5300, 465, 'lenin');

    let tutorialText = this.add.text(
      300,
      -500,
      '        !',
      {
        fontSize: 13,
        fontFamily: '"Press Start 2P"' //    : https://github.com/photonstorm/phaser/blob/v3.16.1/src/gameobjects/text/static/Text.js#L31
      }
    ).setScale(5, 5);
  }

  commissarText(t, d) {
    return {
      type: 'text',
      data: t,
      x: 3300,
      y: 550,
      delay: d,
      duration: 2500,
      scale: 1
    };
  }
}